"use client";

import React, { useEffect, useState } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { usersService } from '@/services/api/users.service';
import { services } from '@/services/api';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Button from '@/components/ui/Button';
import { toast } from 'react-hot-toast';

type ProfileForm = {
  nome: string;
  email: string;
  celular?: string;
};

export default function MinhaContaPage() {
  const { user, loading } = useAuth();
  const [form, setForm] = useState<ProfileForm>({ nome: '', email: '', celular: '' });
  const [saving, setSaving] = useState(false);

  // Alterar senha
  const [senhaAtual, setSenhaAtual] = useState('');
  const [novaSenha, setNovaSenha] = useState('');
  const [confirmSenha, setConfirmSenha] = useState('');
  const [savingPass, setSavingPass] = useState(false);

  useEffect(() => {
    if (user) {
      setForm({
        nome: user.nome || '',
        email: user.email || '',
        celular: (user as any).celular || '',
      });
    }
  }, [user]);

  const handleSave = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!user) return;
    try {
      setSaving(true);
      await usersService.update(user.id, {
        nome: form.nome,
        email: form.email,
        celular: form.celular,
      });
      toast.success('Dados atualizados');
    } catch (err) {
      toast.error('Erro ao atualizar dados');
    } finally {
      setSaving(false);
    }
  };

  const handleChangePassword = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!senhaAtual || !novaSenha) {
      toast.error('Preencha a senha atual e a nova senha');
      return;
    }
    if (novaSenha !== confirmSenha) {
      toast.error('As senhas não conferem');
      return;
    }
    try {
      setSavingPass(true);
      await services.auth.changePassword({ senhaAtual, novaSenha });
      setSenhaAtual('');
      setNovaSenha('');
      setConfirmSenha('');
      toast.success('Senha alterada com sucesso');
    } catch (err) {
      toast.error('Erro ao alterar senha');
    } finally {
      setSavingPass(false);
    }
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white mb-1">Minha Conta</h1>
            <p className="text-sm text-gray-500 dark:text-gray-400">Atualize seus dados pessoais e senha de acesso</p>
          </div>
        </div>
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Dados do usuário */}
          <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6">
            <h2 className="text-lg font-medium text-gray-900 dark:text-white mb-4">Dados do usuário</h2>
            <form onSubmit={handleSave} className="space-y-4">
              <div>
                <Label htmlFor="nome">Nome</Label>
                <Input id="nome" value={form.nome} onChange={(e) => setForm({ ...form, nome: e.target.value })} required />
              </div>
              <div>
                <Label htmlFor="email">Email</Label>
                <Input id="email" type="email" value={form.email} onChange={(e) => setForm({ ...form, email: e.target.value })} required />
              </div>
              <div>
                <Label htmlFor="celular">Celular</Label>
                <Input id="celular" value={form.celular || ''} onChange={(e) => setForm({ ...form, celular: e.target.value })} />
              </div>
              <div className="pt-2">
                <Button type="submit" variant="primary" disabled={saving} className="min-w-40">
                  Salvar
                </Button>
              </div>
            </form>
          </div>

          {/* Alterar senha */}
          <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6">
            <h2 className="text-lg font-medium text-gray-900 dark:text-white mb-4">Alterar senha</h2>
            <form onSubmit={handleChangePassword} className="space-y-4">
              <div>
                <Label htmlFor="senhaAtual">Senha atual</Label>
                <Input id="senhaAtual" type="password" value={senhaAtual} onChange={(e) => setSenhaAtual(e.target.value)} required />
              </div>
              <div>
                <Label htmlFor="novaSenha">Nova senha</Label>
                <Input id="novaSenha" type="password" value={novaSenha} onChange={(e) => setNovaSenha(e.target.value)} required />
              </div>
              <div>
                <Label htmlFor="confirmSenha">Confirmar nova senha</Label>
                <Input id="confirmSenha" type="password" value={confirmSenha} onChange={(e) => setConfirmSenha(e.target.value)} required />
              </div>
              <div className="pt-2">
                <Button type="submit" variant="secondary" disabled={savingPass} className="min-w-40">
                  Alterar senha
                </Button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
